function weatherType(type) {
  let weathericon = '';
  switch(type) {
    case "多云": weathericon = "cloudly";						break;
    case "阴": weathericon = "overcast-sky";						break;
    case "晴": weathericon = "sunshine";					break;
    case "小雨": weathericon = "sprinkle";					break;
    case "阵雨": weathericon = "sprinkle";					break;
    case "中雨": weathericon = "moderateRain";				break;
    case "大雨": weathericon = "heavyRain";			break;
    case "暴雨": weathericon = "rainstorm";			break;
    case "小雪": weathericon = "littleSnow";				break;
    case "中雪": weathericon = "moderateSnow";				break;
    case "大雪": weathericon = "heavySnow";				break;
    case "雨夹雪": weathericon = "sleet";				break;
    case "雷阵雨": weathericon = "thundershower";				break;
    default : weathericon = "icon-Cloud";							break;
  }
  return weathericon;
}
function weatherTypeN(type) {
  let weathericon = '';
  switch(type) {
    case "多云": weathericon = "nightCloudly";						break;
    case "阴": weathericon = "overcast-sky";						break;
    case "晴": weathericon = "sunnyNight";					break;
    case "小雨": weathericon = "sprinkle";					break;
    case "阵雨": weathericon = "sprinkle";					break;
    case "中雨": weathericon = "moderateRain";				break;
    case "大雨": weathericon = "heavyRain";			break;
    case "暴雨": weathericon = "rainstorm";			break;
    case "小雪": weathericon = "littleSnow";				break;
    case "中雪": weathericon = "moderateSnow";				break;
    case "大雪": weathericon = "heavySnow";				break;
    case "雨夹雪": weathericon = "sleet";				break;
    case "雷阵雨": weathericon = "thundershower";				break;
    default : weathericon = "icon-Cloud";							break;
  }
  return weathericon;
}

export{
  weatherType,
  weatherTypeN
}



