function getControlPoints(arr){
  let str='';
    for(let i=0;i<arr.length-1;i++){
      let x,x1,x2,y,y1,point;
        x=arr[i][0]+25;
        x1=arr[i][0]+50
        x2=arr[i+1][0]
      y=60-arr[i+1][1]
        y1=(60-arr[i][1])/2+(60-arr[i+1][1])/2
      point="C"+' '+x+','+y1+','+x1+','+y1+','+x2+','+y+' '
      str+=point
  }
return str
}
function getControlPoints1(arr){
  let str='';
  for(let i=0;i<arr.length-1;i++){
    let x,x1,x2,y,y1,point;
    x=arr[i][0]+25;
    x1=arr[i][0]+50
    x2=arr[i+1][0]
    y=80-arr[i+1][1]
    y1=(80-arr[i][1])/2+(80-arr[i+1][1])/2
    point="C"+' '+x+','+y1+','+x1+','+y1+','+x2+','+y+' '
    str+=point
  }
  return str
}

export{
  getControlPoints,
  getControlPoints1
}



