<template>
  <div id="main">
    <div class="weather">
      <p class="temperature">{{information[0].now.temperature}}
        <span class="circle"></span>
        <span class="text">{{information[0].now.text}}</span>
      </p>
      <div class="wind">
        <p><span class="img wind-direction"></span>{{information[0].now.wind_scale}}级</p>
        <p><img src="../assets/image/humidity.png"> {{information[0].now.humidity}}%</p>
      </div>
    </div>
    <div class="future">
<ul>
  <li v-for="item in information[0].future">
    <p>{{item.day}}</p>
  <p>{{item.date}}</p>
    <p>{{item.text}}</p>
    <p :class="classType(item.text)"></p>
    <div class="svg"></div>
    <p>{{item.night}}</p>
    <p :class="classTypeN(item.night)"></p>
    <p>{{item.wind}}</p>
  </li>
</ul>
      <svg class="svg">
        <text v-for="temp in temp1" :x="x(temp[1],temp[0])" :y="(50-temp[1])" fill="white">{{temp[1]}}</text>
        <circle v-for="temp in temp1" :cx="temp[0]" :cy="(60-temp[1])" r="2" fill="#fff"></circle>
        <path stroke="#fff" stroke-width="2" stroke-opacity="0.5" fill="none" :d="path1"></path>
        <path stroke="#fff" stroke-width="2" stroke-opacity="0.5" fill="none" :d="path2"></path>
        <circle v-for="temp in temp2" :cx="temp[0]" :cy="(80-temp[1])" r="2" fill="#fff"></circle>
        <text v-for="temp in temp2" :x="x(temp[1],temp[0])" :y="(100-temp[1])" fill="white">{{temp[1]}}</text>
      </svg>
    </div>
    <p>生活指数</p>
    <div class="suggestion">
      <div class="box">
        <p><img src="../assets/image/clothes.png"></p>
        <p>{{information[0].today.suggestion.dressing.brief}}</p>
        <p>穿衣</p>
      </div>
      <div class="box">
        <p><img src="../assets/image/sport.png"></p>
        <p>{{information[0].today.suggestion.sport.brief}}</p>
        <p>运动</p>
      </div>
      <div class="box">
        <p><img src="../assets/image/car.png"></p>
        <p>{{information[0].today.suggestion.car_washing.brief}}</p>
        <p>洗车</p>
      </div>
      <div class="box">
        <p><img src="../assets/image/uv.png"></p>
        <p>{{information[0].today.suggestion.uv.brief}}</p>
        <p>紫外线</p>
      </div>
      <div class="box">
        <p><img src="../assets/image/flu.png"></p>
        <p>{{information[0].today.suggestion.flu.brief}}</p>
        <p>感冒</p>
      </div>
      <div class="box">
        <p><img src="../assets/image/travel.png"></p>
        <p>{{information[0].today.suggestion.travel.brief}}</p>
        <p>旅行</p>
      </div>
    </div>
  </div>
</template>

<script>
  import bus from '../assets/eventBUS'
  import {weatherType} from '../assets/js/weatherType.js'
  import {weatherTypeN} from '../assets/js/weatherType.js'
  import {getControlPoints} from '../assets/js/svg.js'
  import {getControlPoints1} from '../assets/js/svg.js'
  import axios from "axios"

  export default {
    data() {
      return {
        information: {},
        data:[],
        suggestion:{},
        temp1:[],
        temp2:[]
      }
    },
    created: function () {
      this.getCity()

    },
    watch: {
      information: {
        deep: true,
        handler: function () {
          this.background(this.information[0].now.text)
        }
      }
    },
    methods: {
      x(tetemperature,num){
        let str=new String(tetemperature)
        if(str.length>1){
          return num-8
        }
        else {
          return num-5
        }
      },
      getCity() {
        bus.$on('userDefindEvent', (data) => {
          axios.get(API_PROXY + 'http://tj.nineton.cn/Heart/index/all?city='+data)
            .then(function (res) {
              this.json(res.data)
            }.bind(this))
            .catch(function (error) {
              console.log(error);
            });
          }
        )
  },

      background(weather) {
        if(new RegExp("阴").test(weather)===true) {
          document.body.className = 'overcast-sky'
        }
        else if(new RegExp("雪").test(weather)===true) {
          document.body.className = 'snows'
        }
        else if(new RegExp("晴").test(weather)===true) {
          document.body.className = 'sunshine'
        }
        else if(new RegExp("雨").test(weather)===true) {
          document.body.className = 'rainy'
        }
        else
        {
          document.body.className = 'cloudly'
        }
        //根据天气状况改变背景
      },
      wind(direction) {
        var box = document.querySelector(".wind-direction");
        switch (direction) {
          case "东":
            break;
          case "东南":
            box.style.transform = 'rotate(45deg)'
            break;
          case "南":
            box.style.transform = 'rotate(90deg)'
            break;
          case "西南":
            box.style.transform = 'rotate(135deg)'
            break;
          case "西":
            box.style.transform = 'rotate(180deg)'
            break;
          case "西北":
            box.style.transform = 'rotate(225deg)'
            break;
          case "北":
            box.style.transform = 'rotate(270deg)'
            break;
          case "东北":
            box.style.transform = 'rotate(315deg)'
            break;
        }
      },
      //根据风向改变图标方向
       json(res){
        var x=-35
         this.temp1=[];
         this.temp2=[];
        for(let i of res.weather[0].future){
          x+=70
          const date=i.date.slice(5);
          i.date=date;
          const text=i.text.split('/');
          i.text=text[0];
          i.night=text[1];
          const wind=i.wind.slice(2);
          i.wind=wind;
          const coordinate1=[x,i.high]
          const coordinate2=[x,i.low]
          this.temp1.push(coordinate1)
          this.temp2.push(coordinate2)
        }
  this.information=res.weather
},
//处理服务器返回的json数据
      point(){
        var y=-35
        for(let i of this.information[0].future){
          y+=70
          const coordinate1=[i.high,y]
          const coordinate2=[i.low,y]
          this.temp1.push(coordinate1)
          this.temp2.push(coordinate2)
        }
      },
      classType(type){
       return   weatherType(type)
      },
      classTypeN(type){
        return   weatherTypeN(type)
      },

    },
    //监听地址，实时获取当前地址的天气信息
    updated() {
      this.wind(this.information[0].now.wind_direction)
      //this.path()
    },
    computed:{
      path1(){
        let path1='M'+' '+this.temp1[0][0]+','+(60-this.temp1[0][1])+' '
        path1+=getControlPoints(this.temp1)
        return path1
      },
      path2(){
        let path1='M'+' '+this.temp2[0][0]+','+(80-this.temp2[0][1])+' '
        path1+=getControlPoints1(this.temp2)
        return path1
      },

    }
  }
</script>

<style lang="scss">
  .overcast-sky {
    background: url("../assets/image/overcast-sky.jpg") no-repeat;
    background-size: cover;
  }

  .sunshine {
    background: url("../assets/image/sunshine.jpg") no-repeat;
    background-size: cover;
  }

  .rainy {
    background: url("../assets/image/rainy.jpg") no-repeat;
    background-size: cover;
  }

  .cloudly {
    background: url("../assets/image/cloudly.jpg") no-repeat;
    background-size: cover;
  }

  .sonws{
    background: url("../assets/image/snows.jpg") no-repeat;
    background-size: cover;
  }

  #main {
    text-align: left;
    padding-left: 2rem;
    .weather {
      .temperature {
        margin-left: 1rem;
        word-spacing: -1.5rem;
        font-size: 7rem;
        line-height: 5.5rem;
        .circle {
          display: inline-block;
          width: 1rem;
          height: 1rem;
          border-radius: 50%;
          vertical-align: 70%;
          border: 0.1rem white solid
        };
        .text {
          font-size: 2.5rem;
          vertical-align: 55%;
        }
      };
      .wind{
        margin-left: 1rem;
        display: flex;
        align-items: center;
        p{
          display: flex;
          align-items: center;
          font-size: 1.5rem;
          margin-right:1rem ;
          img{
            width: 2rem;
            height: 3rem;
          }
          .wind-direction {
            display: inline-block;
            width: 2.5rem;
            height: 2rem;
            background: url("../assets/image/wind-direction.png") no-repeat;
            background-size: 100%;
          };
        }
      };
    };
    .future{
      overflow: auto;
      position: relative;
      svg{
        position: absolute;
        top:12rem;
        left: 0;
        height: 12rem;
        width: 70rem;
      }
      ul{
        width: 70rem;
        padding:0;
        li{
          text-align: center;
          display: inline-block;
          width: 7rem;
         padding: 0 1rem;
          .svg{
            height: 12rem;
            width: 100%;
          }
          .cloudly{
            height: 2rem;
            background: url("../assets/image/cloudly.png") no-repeat center;
            background-size: 50%;
          }
          .sleet{
            height: 2rem;
            background: url("../assets/image/sleet.png") no-repeat center;
            background-size: 50%;
          }
          .sunshine{
            height: 2rem;
            background: url("../assets/image/sunshine.png") no-repeat center;
            background-size: 50%;
          }
          .sprinkle{
            height: 2rem;
            background: url("../assets/image/sprinkle.png") no-repeat center;
            background-size: 50%;
          }
          .overcast-sky{
            height: 2rem;
            background: url("../assets/image/overcast-sky.png") no-repeat center;
            background-size: 50%;
          }
          .moderateRain{
            height: 2rem;
            background: url("../assets/image/moderateRain.png") no-repeat center;
            background-size: 50%;
          }
          .heavyRain{
            height: 2rem;
            background: url("../assets/image/heavyRain.png") no-repeat center;
            background-size: 50%;
          }
          .thundershower{
            height: 2rem;
            background: url("../assets/image/heavyRain.png") no-repeat center;
            background-size: 50%;
          }
          .rainstorm{
          height: 2rem;
          background: url("../assets/image/rainstorm.png") no-repeat center;
          background-size: 50%;
        }
          .littleSnow{
        height: 2rem;
        background: url("../assets/image/littleSnow.png") no-repeat center;
        background-size: 50%;
        }
          .moderateSnow{
          height: 2rem;
          background: url("../assets/image/moderateSnow.png") no-repeat center;
          background-size: 50%;
        }
          .heavySnow{
          height: 2rem;
          background: url("../assets/image/heavySnow.png") no-repeat center;
          background-size: 50%;
        }
          .nightCloudly{
            height: 2rem;
            background: url("../assets/image/nightCloudly.png") no-repeat center;
            background-size: 50%;
          }
          .sunnyNight{
          height: 2rem;
          background: url("../assets/image/sunnyNight.png") no-repeat center;
          background-size: 50%;
        }
        }
      }

    }
    .suggestion {
      margin: auto;
      display: flex;
      flex-wrap: wrap;
      .box {
        text-align: center;
        width: 9rem;
        padding:0 2rem;
        img {
          width: 3rem;
        }
      }
    }
  }
</style>



