<template>
  <div class="search" v-if="visible">
    <div class="header">
    <div class="cc" v-on:click="close"></div>
    <form>
        <input type="text" placeholder="搜索全国城市" v-model="city">
      <button type="submit" class="btn btn-default" v-on:click="search">搜索</button>
    </form>
  </div>
    <div class="city">
      <p>热门城市</p>
<ul>
  <li v-for="item in hotCity" v-on:click="data(item)">{{item}}</li>
</ul>
    </div>
  </div>

</template>

<script>
  import bus from '../assets/eventBUS'

  export default {
    data(){
      return {
        hotCity:['北京','上海','天津','重庆','成都','广州','深圳','杭州','长沙','青岛',
          '大理','西安','沈阳','南京','苏州','无锡','郑州','宁波','苏州','三亚'],
        visible:false,
        city:''
      }
    },
    created(){
      this.get()
    },
    methods:{
      close(){
        this.visible=!this.visible
        bus.$emit('close', 'true')
      },
      search(){
        this.visible=!this.visible
        bus.$emit('city', this.city)
      },
      get() {
        bus.$on('open', (data) => {
          this.visible = data
        })
      },
       data(item) {
        bus.$emit('hot', item)
        this.visible=!this.visible
      }
    },
    updated(){
      $(".search").css({
        height: function () {
          return $(document).height();
        },
        width:"100%"
      })
    }

  }
</script>

<style lang="scss">
  .search{
    position: absolute;
    top:0;
    z-index: 10000;
      background-color: white;
    color: black;
    .header {
      height: 20rem;
      background: url("../assets/image/city.jpg") no-repeat center;
      background-size: 100%;
      .cc{
        height: 2rem;
        width: 2rem;
        background: url("../assets/image/close.png") no-repeat center;
        background-size: 100%;
      }
      form{
        width: 100%;
        display:block;
        margin: 9rem 0;
        text-align: center;
      }
    };
    .city{
      p{
        text-align: center;
        margin-top:2rem ;
      }
      ul{
        display: flex;
        flex-wrap: wrap;
        padding: 0;
      li{
        display: inline-block;
        border:1px black solid;
        border-radius: 50%;
        width: 20%;
        margin: 1rem 2rem;
        padding:0.5rem 0;
        list-style: none;
      }
      }
    }
  }
</style>



