<template>
  <div id="header">
    <div class="menu">
      <div v-on:click="open"><span class="glyphicon glyphicon-list"></span></div>
      <p>{{city}}市</p>
      <span class="glyphicon glyphicon-map-marker"></span>
    </div>
  </div>
</template>

<script>
  import bus from '../assets/eventBUS'
  import axios from "axios"

  export default {
    data() {
      return {
        list:[],
        cityId: '',
        city:'',
        visible:true
      }
    },
    created() {
      this.data()
    },
   watch:{
    city:function () {
  this.search()

}
    },
    methods: {
  location() {
    return this.$jsonp('https://api.map.baidu.com/location/ip?ak=rrOrzPtWySxTdcX59tcOgw0PGzQtrZnb').then(json => {
    return (json.content.address_detail.city.slice(0,2))
  }).catch(err => {
    console.log(err)
  })

  },
      //通过百度地图api以ip方式获取地理位置
      //用过 proxyTable 参数配置，实现跨域
      getCity() {
      return  axios.get(API_PROXY + 'http://www.wanandroid.com/tools/mockapi/4395/czw')
          .then(function (res) {
            return res.data.list
          }.bind(this))
          .catch(function (error) {
            console.log(error);
          });
        },
      //获取中央天气地址参数的json数据
      change() {
        for (let a of this.list) {
          const city = this.city
          if (city === a.cityName && city === a.townName) {
            return a.townID
          }
        }
      },
      //将地址转换为对应中央天气api的城市id
      send() {
        bus.$emit('userDefindEvent', this.cityId)
      },
      open() {
        bus.$emit('open', this.visible)
      },
      //将城市id传到weather组件去
      async data() {
        this.city= await this.location()
        this.list = await this.getCity()
        this.cityId = await this.change()
        this.send()
      },
      //将异步函数以同步方式实现，保证正确获取数据
      async search() {
        this.cityId = await this.change()
        this.send()
      }
    },
    mounted(){
      bus.$on('city', (data) => {
        this.city=data
      }),
        bus.$on('hot', (data) => {
          this.city=data
        })
    }
  }
</script>

<style lang="scss">
  #header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .menu {
      padding-top: 2rem;
      display: flex;
      align-items: center;
      p {
        margin: 0;
      }
      .glyphicon-list {
        display: inline-block;
        margin: 0 1rem 0 2rem;
      }
    }
    }


</style>



